@media screen and (min-width: 767px) {
    #masthead.site-header .main-navigation ul.nav-menu {
        max-width: 100%;
    }

    .section-title {
        font-size: 60px;
    }

    .col-3 .hentry {

        width: 33.33%;
        float: left;
    }

    .col-4 .hentry {
        width: 25%;
        float: left;
    }

    .col-5 .hentry {
        width: 20%;
        float: left;
    }

    .col-6 .hentry {
        width: 16.66%;
        float: left;
    }

    #about {
        .col-3 article {
            width: 33.33%;
            float: left;

            &:nth-child(3n+1) {
                clear: left;
            }

            &:nth-last-child(-n+3) {
                margin-bottom: 0;
            }
        }
    }



    #blog {
        .section-content {
            padding-top: 50px;
        }

        .col-3 article {
            width: 33.33%;

            &:nth-child(2n+1) {
                clear: none;
            }

            &:nth-child(3n+1) {
                clear: left;
            }

            &:nth-last-child(-n+3) {
                margin-bottom: 0;
            }
        }
    }



    .footer-widgets-area.col-3 {
        .hentry {
            width: 33.33%;
            float: left;
            margin-bottom: 0;
        }

        &:not(:last-child) {
            padding-right: 40px;
        }

    }


    #eventgoal .col-3 article:nth-child(3n+1) {
        clear: left;
    }




    #courses,
    #featured-videos {
        .service-content .responsive-container .light {
            width: 120%;
            border: 1px solid #f6f4f3;
            border-top-color: #fff;
            border-radius: 3px;
            border-top-color: #fff;
            border-radius: 3px;
            background-color: #ebe8e5;
            box-shadow: 0 0 2px 2px rgba(0, 0, 0, .15);
            -webkit-box-shadow: 0 0 2px 2px rgba(0, 0, 0, .15);
            -moz-box-shadow: 0 0 2px 2px rgba(0, 0, 0, .15);
            left: 50%;
            -webkit-transform: translateX(-50%);
            -moz-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
            -o-transform: translateX(-50%);
            transform: translateX(-50%);
        }
    }

    #counter .section-content.col-3 .inner {
        width: 33.33%;
        float: left;
    }

    #counter .section-content.col-3 .inner:nth-child(3n+1) {
        clear: left;
    }

    #counter .section-content.col-3 .inner:nth-last-child(-n+3) {
        margin-bottom: 0;
    }

    #counter .section-content.col-4 .inner {
        width: 25%;
        float: left;
    }

    #counter .section-content.col-4 .inner:nth-child(2n+1) {
        clear: none;
    }

    #counter .section-content.col-4 .inner:nth-child(4n+1) {
        clear: left;
    }

    #counter .section-content.col-4 .inner:nth-last-child(-n+4) {
        margin-bottom: 0;
    }

    #counter .section-content.col-5 .inner {
        width: 33.33%;
        float: left;
    }

    #counter .section-content.col-5 .inner:nth-child(2n+1) {
        clear: none;
    }


    #counter .section-content.col-5 .inner:nth-child(3n+1) {
        clear: left;
    }

    #counter .section-content.col-5 .inner:nth-last-child(-n+2) {
        width: 48%;
        display: inline-block;
        float: none;
    }

    .site-info.col-2 {
        span {
            float: left;
            width: 50%;
            text-align: left;
        }

        &:nth-child(2) {
            float: right;
            text-align: right;
        }

    }

    .site-info {
        padding: 10px 0;
    }

    #main #comments {
        width: 100%;
    }

    #main .not-logged .light {
        width: 90%;

    }
}