.responsive-container {
    position: relative;
    width: 100%;
    height: auto;
    margin: 0 auto;
}


.light {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    z-index: 99999;
    overflow: visible;
    width: 100%;
    height: 100%;

    .container-btn {
        background-color: $white;
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        z-index: 5;
    }

}

#courses,
#featured-videos {
    .service-content .responsive-container .light {
        width: 116%;
        border: 1px solid #f6f4f3;
        border-top-color: #fff;
        border-radius: 3px;
        border-top-color: #fff;
        border-radius: 3px;
        background-color: #ebe8e5;
        box-shadow: 0 0 2px 2px rgba(0, 0, 0, .15);
        left: 50%;
        transform: translateX(-50%);
    }

}

.img-preloader {
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: auto;
}

.img-none {
    visibility: hidden;
}

.play-icon {
    position: absolute;
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);
    width: 80px;
    height: 80px;
    cursor: pointer;
    z-index: 3;

    transition: all 0.5s ease-in-out;

}

.title-video {
    position: absolute;
    top: 10%;
    left: 50%;

    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
    color: #333;

}



.video-preloader {
    position: absolute;
    top: 0;
    right: 0;
}



.closebtn {
    float: right;
    margin-right: 10px;
    padding: 5px 15px;
    border: none;
    font-size: 32px;
    background-color: transparent;

    &:hover {
        color: $red;
    }
}

.wp-video {
    margin: 0 auto;
}



.wp-video iframe,
.wp-video object,
.wp-video embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

