.mejs-controls {
    display: none;
}

.mejs-overlay-play {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: auto !important;
    height: auto !important;
}

#headerPopup {
    width: 75%;
    margin: 0 auto;
}

#headerPopup .mejs-video {
    width: 100%;
    margin: 0 auto;
}

.videos-container {
    display: flex;
    flex-wrap: wrap;
}


.entry-container .video-title {
    color: #333;
    padding: 20px 0 20px 15px;
}


.videos-container .video {
    min-width: 350px;
    width: 100%;
    padding: 10px 20px;
}

.wp-video {
    display: inline-block;
    position: relative;
    padding-bottom: 56%;
    width: 100%;
}

.wp-video iframe,
.wp-video object,
.wp-video embed,
.wp-video .mejs-container {
    height: 100% !important;
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
}

video.wp-video-shortcode,
.mejs-container,
.mejs-overlay.load {
    width: 100% !important;
    height: 100% !important;
}


.wp-video:has(.mejs-container) {
    padding-bottom: 0;
}