/* Normalize
--------------------------------------------- */
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Muli', sans-serif;
    font-weight: 400;
    scroll-behavior: smooth;
}

html {
    font-family: sans-serif;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    width: 100%;
    overflow-x: hidden;
}

#page {
    max-width: 1920px;
    margin: 0 auto;
    box-shadow: 0 0 15px #aaa;
    -webkit-box-shadow: 0 0 15px #aaa;
    -moz-box-shadow: 0 0 15px #aaa;
}

.boxed-layout {
    max-width: 1600px;
    width: 90%;
    margin: 0 auto;
}

.frame-layout {
    max-width: 1600px;
    width: 90%;
    margin: 50px auto;
}

#content {
    position: relative;
    width: 90%;
    margin: 30px auto 0;
    background-color: #fff;
}

article {
    padding: 0 20px;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
    display: block;
}

audio,
canvas,
progress,
video {
    display: inline-block;
    vertical-align: baseline;
}

audio:not([controls]) {
    display: none;
    height: 0;
}

[hidden],
template {
    display: none;
}



a {
    background-color: transparent;
}

a:active,
a:hover {
    outline: 0;
}

a:focus {
    outline: thin dotted;
    color: #fff;
}

abbr[title] {
    border-bottom: 1px dotted;
}

b,
strong {
    font-weight: bold;
    color: $black36;
}

dfn {
    font-style: italic;
}

h1 {
    font-size: 52px;
}

h2 {
    font-size: 42px;
}

h3 {
    font-size: 26px;
}

h4 {
    font-size: 20px;
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 14px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    clear: both;
    color: #333;
    margin: 16px 0;
    line-height: 1.3;
    font-weight: 400;
    font-family: 'Oxygen', cursive;
}


mark {
    background: #222;
    color: #fff;
}

small {
    font-size: 80%;
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sup {
    top: -0.5em;
}

sub {
    bottom: -0.25em;
}

img {
    border: 0;
}

figure {
    margin: 1em 40px;
}


hr {
    box-sizing: content-box;
    height: 0;
}

pre {
    overflow: auto;
}

code,
kbd,
pre,
samp {
    font-family: monospace, monospace;
    font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
    color: inherit;
    font: inherit;
    margin: 0;
}

input:focus,
button:focus,
textarea:focus {
    outline: dotted;
    color: #fff;
}

button {
    overflow: visible;
}

button,
select {
    text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer;
}

button[disabled],
html input[disabled] {
    cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

input {
    line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

legend {
    border: 0;
    padding: 0;
}

textarea {
    overflow: auto;
}

optgroup {
    font-weight: bold;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

tbody {
    text-align: left;
}

tr {
    border: 1px solid #e4e4e4;
}

td,
th {
    padding: 10px 25px;
    font-weight: 400;
}

th {
    color: #fff;
}

td#next {
    text-align: right;
}