@media screen and (min-width: 567px) {

    #masthead .header-top .wrapper .container {
        width: 70%;
    }

    .videos-container .video {
        width: 50%;
        height: 50%;
    }

    .section-title {
        font-size: 45px;
    }


    .blog-posts-wrapper {
        .col-3 article {
            width: 50%;
            float: left;
        }

        .col-3 article:nth-last-child(-n+2) {
            margin-bottom: 0;
        }

        .col-3 article:nth-child(2n+1) {
            clear: left;
        }
    }

    #counter .section-content.col-2 .inner {
        width: 50%;
        float: left;
    }

    #counter .section-content.col-2 .inner:nth-child(2n+1) {
        clear: left;
    }

    #counter .section-content.col-2 .inner:nth-last-child(-n+2) {
        margin-bottom: 0;
    }

    #counter .section-content.col-5 .inner:last-child {
        width: 100%;
        float: none;
    }

    .site-branding .site-logo .custom-logo-link:first-child {
        margin-right: 30px;
    }




    // #featured-slider {
    //     .slick-item {
    //         padding: 235px 0;
    //     }
    // }

    #gallery-slider .slick-item {
        padding: 220px 0;
    }

    .blog-posts-wrapper .col-3 article {
        width: 50%;
        float: left;
    }

    .footer-widgets-area.col-2 .hentry,
    .footer-widgets-area.col-4 .hentry {
        width: 50%;
        float: left;
    }

    .footer-widgets-area.col-2 .hentry:nth-child(2n+1),
    .footer-widgets-area.col-4 .hentry:nth-child(2n+1) {
        clear: left;
    }

    .footer-widgets-area.col-2 .hentry:nth-child(odd),
    .footer-widgets-area.col-4 .hentry:nth-child(odd) {
        padding-right: 40px;
    }

    .footer-widgets-area.col-2 .hentry:nth-last-child(-n+2),
    .footer-widgets-area.col-4 .hentry:nth-last-child(-n+2) {
        margin-bottom: 0;
    }


}