@media screen and (min-width: 992px) {

    #courses,
    #featured-videos {
        article {
            display: flex;
        }

        .section-content .articles-container article {
            width: 47%;
        }

        .section-content .articles-container article {

            video {
                width: 100%;
            }
        }

        .section-content {
            position: relative;

            // &:before {
            //     content: '';
            //     display: block;
            //     padding-top: 100%;
            // }

            .articles-container {
                // position: absolute;
                // top: 0;
                // bottom: 0;
                // right: 0;
                // left: 0;
                // line-height: 100%;
                height: 100%;
                -webkit-display: flex;
                -moz-display: flex;
                -ms-display: flex;
                -o-display: flex;
                display: flex;
                -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
                justify-content: space-between;
            }
        }


    }



    #top-bar {
        .widget+.widget {
            margin-top: 0;
        }
    }

    #top-bar.col-2 {
        .widget {
            float: left;
            text-align: left;
            padding: 6px 0;
        }

        .widget+.widget {
            float: right;
            text-align: right;
            padding: 0;
        }

    }

    .featured-content-wrapper {
        .entry-title {
            font-size: 40px;
        }
    }

    .about-item-wrapper {
        .entry-title {
            font-size: 42px;
        }
    }

    #blog {
        .col-4 article {
            width: 25%;
        }

        .col-4 article:nth-child(2n+1) {
            clear: none;
        }

        .col-4 article:nth-child(4n+1) {
            clear: left;
        }

        .col-4 article:nth-last-child(-n+4) {
            margin-bottom: 0;
        }
    }



    #counter .section-content.col-5 .inner {
        width: 20%;
    }

    #counter .section-content.col-5 .inner:nth-child(2n+1),
    #counter .section-content.col-5 .inner:nth-child(3n+1) {
        clear: none;
    }

    #counter .section-content.col-5 .inner:nth-child(5n+1) {
        clear: left;
    }

    #counter .section-content.col-5 .inner:nth-last-child(-n+2) {
        width: 20%;
        float: left;
    }

    #counter .section-content.col-5 .inner:nth-last-child(-n+5) {
        margin-bottom: 0;
    }

    nav.navigation.pagination {
        margin: 63px 0;
    }

    #colophon .social-icons {
        margin-left: -15px;
    }

    .social-icons li:not(:last-child) {
        margin-right: 14px;
    }

    .footer-widgets-area.col-4 {
        .hentry {
            width: 25%;
            padding-right: 40px;

            &:last-child {
                padding-right: 0;
            }

            &:nth-child(2n+1) {
                clear: none;
            }

            &:nth-child(4n+1) {
                clear: left;
            }

            &:nth-last-child(-n+4) {
                margin-bottom: 0;
            }

        }

    }



}